import React from "react";
import "./MemberDetailModal.css"; // Import the CSS

const MemberDetailModal = ({ member, onClose }) => {
  if (!member) return null;

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <button className="close-btn" onClick={onClose}>
          
        </button>
        <div className="member-container">
          {/* Member's Info */}
          <div className="member-info">
            <img
              src={member.photo}
              alt={member.name}
              className="member-photo"
              onError={(e) => {
                e.target.src =
                  "https://avatar.canva.com/avatars/users/9c33b441-17b5-45da-b517-7415680898e6/200.jpg";
              }}
            />
            <h2>{member.name}</h2>
          </div>

          {/* Heart in the Middle */}
          {member.spouse && <span className="heart"></span>}

          {/* Spouse's Image and Name (if available) */}
            {member.spouse && (
              <div className="spouse-info visible">
                <img
                  src={member.spouse.photo}
                  alt={member.spouse.name}
                  className="spouse-photo"
                  onError={(e) => {
                    e.target.src =
                      "https://avatar.canva.com/avatars/users/9c33b441-17b5-45da-b517-7415680898e6/200.jpg";
                  }}
                />
                <h2>{member.spouse.name}</h2>
              </div>
            )}
        </div>

        {/* Children List */}
        {member.children?.length > 0 && (
          <div className="children-section">
            <h3> Children:</h3>
            <ul>
              {member.children.map((child, index) => (
                <li key={index}>{child.name}</li>
              ))}
            </ul>
          </div>
        )}
      </div>
    </div>
  );
};

export default MemberDetailModal;
