// src/FamilyTree.js
import React from 'react';
import Tree from 'react-d3-tree';
import CustomNode from './CustomNode';

const FamilyTree = ({ member, onSelect }) => {
    return (
        <div id="treeWrapper" 
        style={{ 
          width: '80%',
          height: '500px',
          margin: 'auto', // Centers horizontally
          display: 'flex',
          justifyContent: 'center', // Centers content inside
          alignItems: 'center', // Centers vertically
          border: '2px solid #ddd', // Add border to visualize the tree area
          borderRadius: '10px',
          padding: '10px',
          backgroundColor: '#f9f9f9' // Light background to distinguish the tree
        }}>
          <Tree
            data={member}
            orientation="vertical"
            translate={{ x: 300, y: 100 }}
            pathFunc="step"
            collapsible={false}
            zoom={0.8}
            zoomable={true} // Disable zoom on scroll
            separation={{ siblings: 1.5, nonSiblings: 2 }} // Spacing
            renderCustomNodeElement={(nodeDatum) => (
              <CustomNode nodeDatum={nodeDatum} onSelect={onSelect} />
            )}
          />
        </div>
    );
  };
  
  export default FamilyTree;