import React, { useState } from "react";
import FamilyTree from "./FamilyTree";
import MemberDetailModal from "./MemberDetailModal";
import familyMotherRahadianSide from "./data/familyMotherRahadianSide";
import familyFatherRahadianSide from "./data/familyFatherRahadianSide";
import "./App.css";

const familyOptions = {
  MotherRahadianSide: familyMotherRahadianSide,
  FatherRahadianSide: familyFatherRahadianSide,
};

function App() {
  const [familyData, setFamilyData] = useState(familyMotherRahadianSide);
  const [newMember, setNewMember] = useState({
    name: "",
    parent: "",
    photo: "",
    isSpouse: false, // NEW: To determine if adding a spouse
  });
  const [showForm, setShowForm] = useState(false); //  New state for form visibility
  const [selectedMember, setSelectedMember] = useState(null);
  const [selectedFamily, setSelectedFamily] = useState("MotherRahadianSide");

  const handleAddMember = (e) => {
    e.preventDefault();
    const updatedFamilyData = { ...familyData };
    const targetMember = findMember(updatedFamilyData, newMember.parent);

    if (targetMember) {
      if (newMember.isSpouse) {
        // Add as spouse
        if (targetMember.spouse) {
          alert(`${targetMember.name} already has a spouse!`);
        } else {
          targetMember.spouse = {
            name: newMember.name,
            photo: newMember.photo,
          };
        }
      } else {
        // Add as child
        if (!targetMember.children) {
          targetMember.children = [];
        }
        targetMember.children.push({
          name: newMember.name,
          photo: newMember.photo,
        });
      }
      setFamilyData(updatedFamilyData);
      setNewMember({ name: "", parent: "", photo: "", isSpouse: false });
      setShowForm(false); //  Hide form after submission
    } else {
      alert("Parent/Member not found!");
    }
  };

  const findMember = (member, targetName) => {
    if (member.name === targetName) {
      return member;
    }
    if (member.children) {
      for (let child of member.children) {
        const found = findMember(child, targetName);
        if (found) return found;
      }
    }
    return null;
  };

  if (!familyData) {
    return <h2>Loading Family Tree...</h2>; // Show loading text while data loads
  }

  return (
    <div className="app-container">
      <h1>Family Tree</h1>

      <select
        onChange={(e) => setSelectedFamily(e.target.value)}
        value={selectedFamily}
      >
        {Object.keys(familyOptions).map((key) => (
          <option key={key} value={key}>
            {key.replace(/([A-Z])/g, " $1")} {/* Formats "MotherSide" -> "Mother Side" */}
          </option>
        ))}
      </select>

      {/* Floating Button - Visible Only When Form is Hidden */}
      {!showForm && (
        <button className="floating-add-btn" onClick={() => setShowForm(true)}>
           Add Member
        </button>
      )}

      <div
        className={`tree-form-container ${
          showForm ? "form-visible" : "form-hidden"
        }`}
      >
        <div className="tree-container">
          <FamilyTree member={familyOptions[selectedFamily]} onSelect={setSelectedMember} />
        </div>

        <div className="form-container">
          {/* FORM TO ADD MEMBERS & SPOUSES */}
          <button
            className="toggle-form-btn"
            onClick={() => setShowForm(false)}
          >
             Hide Form
          </button>

          {showForm && ( //  Show form only when `showForm` is true
            <form onSubmit={handleAddMember} className="expandable-form">
              <input
                type="text"
                placeholder="Name"
                value={newMember.name}
                onChange={(e) =>
                  setNewMember({ ...newMember, name: e.target.value })
                }
              />
              <input
                type="text"
                placeholder="Parent/Spouse Name"
                value={newMember.parent}
                onChange={(e) =>
                  setNewMember({ ...newMember, parent: e.target.value })
                }
              />
              <input
                type="text"
                placeholder="Photo URL"
                value={newMember.photo}
                onChange={(e) =>
                  setNewMember({ ...newMember, photo: e.target.value })
                }
              />

              <label>
                <input
                  type="checkbox"
                  checked={newMember.isSpouse}
                  onChange={(e) =>
                    setNewMember({ ...newMember, isSpouse: e.target.checked })
                  }
                />
                Add as Spouse
              </label>

              <button type="submit">Add Member</button>
            </form>
          )}
        </div>
      </div>

      {selectedMember && (
        <MemberDetailModal member={selectedMember} onClose={() => setSelectedMember(null)} />
      )}
    </div>
  );
}

export default App;
